/**
* @author gm
* @description 取消预订原因弹窗
* @date 2017-05-17
**/
import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import classnames from 'classnames';

import {Modal} from 'antd';  
import Scrollbars from 'react-custom-scrollbars';

import './cancel_booking_reason_popup.less';
import '../modal.css';

@inject('bookingStore') @observer
class CancelBookingReasonPopup extends Component {

	constructor(props){
		super(props);
		this.state={reason:'',current:-1}
		if(this.props.bookingStore.recordItem){
			this.props.bookingStore.queryRefundScale(this.props.bookingStore.recordItem.bookingID);
		}
		
	}

	handleOk=()=>{
		if(this.props.okClick){
			this.props.okClick();
		}
		this.props.bookingStore.cancelBookingOk(this.state.reason,this.props.bookingStore.recordItem.bookingID);
	}

	handleCancel=()=>{
		if(this.props.cancelClick){
			this.props.cancelClick();
		}
	}

	reasonOnChange=(e)=>{
		this.setState({reason:e.target.value})
	}

	render() {

		let bookingStore = this.props.bookingStore;
		
    	return <div>
			<Modal  
                title="取消预订"
				visible={true} 
				maskClosable={false} 
				footer={null}
				width={676}
				closable={false}
				wrapClassName="cancel-booking-reason-popup-modal"    
				>      
				<div className="cancel-booking-reason-container">

					<div className="reason-input">
						<input type="text" placeholder="请选择／输入取消预订原因" value={this.state.reason} onChange={this.reasonOnChange}/>
					</div>

					<div className="reason-btns">
						<Scrollbars>
							<ul>
								{bookingStore.cancelReasons&&bookingStore.cancelReasons.cause.map((reason,index)=>{
									return <li className={classnames({
										"btn-item":true,
										"select":index===this.state.current
									})} key={index} onClick={()=>{
										this.setState({current:index});
										this.setState({reason:reason});
									}}>
										{reason}
									</li>
								})}								
							</ul>
						</Scrollbars>
					</div>					
				</div>
				<div className="cancel-booking-reason-btns">
					<div onClick={this.handleCancel}>取消</div>
					<div onClick={this.handleOk}>确定</div>
				</div>
			</Modal>
      </div>
	}

}

export default CancelBookingReasonPopup;



