/* 微信扫码 */
import React from 'react';
import { Modal } from 'antd';


import './wechat_pay_popup.less';


class WechatPay extends React.Component {
  constructor(props){
    super(props);
    this.state={inputValue:''}
  }
 

  render() {
    // 取消，先暂时放这
    let cance=<button className="cancle" onClick={()=>{
              if(this.props.onCancel){
                this.props.onCancel();
              }
            }}>取消</button>
    return (
      <div id="wechat-pay">
        <Modal title={this.props.title}
            visible={true} 
            maskClosable={false} 
            okText='确定' cancelText='取消'
            // footer={null}
            width={500}
            wrapClassName="wechat-pay-modal"
            onCancel={()=>{
              if(this.props.onCancel){
                this.props.onCancel();
              }
            }}
            onOk={()=>{
              if(this.props.onOk){
                this.props.onOk(this.state.inputValue);
              }
            }}
        >
          <div>
            <div className="change-tab"></div>
            <p className="wechat-text">本次收款<span className="wechat-amount">{this.props.inputValue}</span>元，请扫描客户的付款码！</p>
            <p className="wechat-text">付款码<input type="text" value={this.state.inputValue} onChange={(e)=>{
                this.setState({inputValue:e.target.value})
            }}/></p>
            
          </div>
        </Modal>
      </div>
    )
  }
}

export default WechatPay;


