import React from 'react';
import './kong_block.less';

class KongBlock extends React.Component {

  render() {
     
    return (
        <div className="my-kong-block">
            <div className="icon-block">
                <i className="iconfont icon-zhuotaixinxi_icon_kongtai-"></i>
            </div>           
        </div>
    );
  }
}


export default KongBlock;


