/**
* @author gm
* @description 预定时间弹窗
* @date 2017-05-17
**/
import React, { Component } from 'react';
import {Modal, Tabs, Alert} from 'antd';  
import moment from 'moment';
import { inject, observer } from 'mobx-react';
import classnames from 'classnames';

import Scrollbars from 'react-custom-scrollbars';
import HorizontalDatePicker from './horizontal-datepicker';

import './booking_time_popup.less';
import '../modal.css';

const TabPane = Tabs.TabPane;

@inject('bookingStore') @observer
class BookingTimePopup extends Component {

    constructor(props){
        super(props);



        this.props.bookingStore.getMealsInfo(moment().format("YYYY-MM-DD"));

        this.state={
            calendar:'',
            currentTime:'',
            alertPopup:'',
            submitTime:moment().format("YYYY-MM-DD")
        }
       
    }
    
    jianClick=()=>{

        this.props.bookingStore.timeJianClick();

       
    }
    jiaClick=()=>{
         this.props.bookingStore.timeJiaClick();
       
    }

    tabClick=(key)=>{
         this.props.bookingStore.timeTabClick(key);
    }

	render() {
        let bookingStore = this.props.bookingStore;
        
        return <div>
			<Modal  
				visible={true} 
				maskClosable={false} 
				footer={null}
				width={600}
				closable={false}
				wrapClassName="booking-time-popup-modal"    
				>      
				<HorizontalDatePicker dateClick={(date)=>{
                   this.setState({currentTime:''});
                   this.setState({submitTime:date});
                   console.log("date"+date);
                   bookingStore.getMealsInfo(date);

                }} calendarClick={(time)=>{
                    this.setState({currentTime:''});
                     bookingStore.getMealsInfo(time);
                }}/>
                <div className="booking-time-container">
                    <div className="time-block">
                        <div className="all-time-point">
                            <Tabs defaultActiveKey="0" onTabClick={(key)=>{
                                this.tabClick(key);
                            }}>
                                {bookingStore.mealsInfos.map((meal,index)=>{

                                    let timeItem = meal.rserveTimeList.map((time,mindex)=>{
                                       
                                        return   <li key={mindex} className={classnames({
                                            "select":time===this.state.currentTime,
                                        })} onClick={()=>{
                                           
                                                 this.setState({currentTime:time});
                                                 this.setState({alertPopup: ''});     
                                                                                                          
                                        }}>{time}</li>
                                    })
                                    return <TabPane tab={meal.mealName} key={index}>
                                        <Scrollbars>
                                            <ul className="time-lis">
                                                     {timeItem}                                                                  
                                            </ul>                                   
                                        </Scrollbars>                               
                                    </TabPane>
                                })}
                                
                            </Tabs>            
                        </div>   

                        <div className="duration">
                            <p>就餐时长（单位：分钟）</p>
                            <div><i className="iconfont icon-jian" onClick={()=>{
                                this.jianClick()
                            }}></i>{bookingStore.mealsDuration.segmentLength}<i className="iconfont icon-jia" onClick={()=>{
                                this.jiaClick();
                            }}></i></div>
                        </div>        
                    </div>               
					<div className="confirm-btn" onClick={()=>{

                        if(this.state.currentTime){
                          
                                let result = this.state.submitTime+" "+this.state.currentTime+':00';  

                                bookingStore.selectTimeClick(result);

                                if(this.props.timeClick){
                                    this.props.timeClick();
                                }
                            
                        }else{
                            this.setState({alertPopup: <Alert message="请选择餐段" banner closable onClose={()=>{
                                this.setState({alertPopup:""});
                            }}/>});                           
                        }
                       
                    }}>确定</div>
                     {this.state.alertPopup}
				</div>
                
			</Modal>
           
           
      </div>
	}

}

export default BookingTimePopup;



