/**
* @author gm
* @description 客人查询弹窗
* @date 2017-05-17
**/
import React, { Component } from 'react';
import {Modal} from 'antd';  
import './guest_search_popup.less';
import Scrollbars from 'react-custom-scrollbars';

class GuestSearchPopup extends Component {

	render() {
    return <div>
			<Modal  
                title="客人查询"
				visible={true} 
				maskClosable={false} 
				okText='确定' cancelText='取消'
				width={840}
				closable={false}
				wrapClassName="guest-search-popup-modal"    
				>      
				<div className="guest-search-container">
					<div className="search">
						<div className="calendar"></div>
						<input type="text" placeholder="客人、电话、桌台"/>
						<div className="search-btn">
							<i className="iconfont icon-order_btn_search"></i>
							搜索
						</div>
					</div>
					<div className="content">
						<div className="title">
							<span>序号</span>
							<span>预订时间</span>
							<span>客人</span>
							<span>预打电话</span>
							<span>人数</span>
							<span>桌台</span>
							<span>状态</span>
							<span>备注</span>
						</div>
						<div className="items-block">
							<Scrollbars>
								<div className="item">
									<span>1</span>
									<span>04-16  19:30</span>
									<span>刘女士</span>
									<span>13723458769</span>
									<span>4</span>
									<span>B02</span>
									<span>完成</span>
									<span></span>
								</div>	
								<div className="item">
									<span>1</span>
									<span>04-16  19:30</span>
									<span>刘女士</span>
									<span>13723458769</span>
									<span>4</span>
									<span>B02</span>
									<span>完成</span>
									<span></span>
								</div>						
							</Scrollbars>
						
						</div>
						
					</div>
				</div>
			</Modal>
      </div>
	}

}

export default GuestSearchPopup;



