/**
* @author gm
* @description 支付小票弹窗
* @date 2017-05-17
**/
import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import {Modal} from 'antd';  
import moment from 'moment';

import './payment_receipt_popup.less';

@inject('bookingStore') @observer
class PayReceiptPopup extends Component {


	okClick=()=>{
		if(this.props.okClick){
			this.props.okClick();
		}
	}
	render() {
		let bookingStore = this.props.bookingStore;
    	return <div>
			<Modal  
				visible={true} 
				maskClosable={false} 
				footer={null}
				width={522}
				closable={false}
				wrapClassName="receipt-popup-modal"    
				>      
				<div className="receipt-container">
					<div className="receipt-title">{bookingStore.bookingSuccessInfo.merchantName}预订支付小票</div>
					<ul className="receipt-info">
						<li><span className="title">预订单号：</span>{bookingStore.bookingSuccessInfo.bookingCode}</li>
						<li><span className="title">电话号码：</span>{bookingStore.bookingSuccessInfo.phone}</li>
						<li><span className="title">预  订 人：</span>{bookingStore.bookingSuccessInfo.contact}</li>
						<li><span className="title">预订时间：</span>{moment(bookingStore.bookingSuccessInfo.bookingTime).format("MM-DD HH:mm")}</li>
						<li><span className="title">人   数：</span>{bookingStore.bookingSuccessInfo.peopleNum}</li>
						<li><span className="title">预订桌台：</span>{bookingStore.bookingSuccessInfo.tableCodes}</li>
						<li><span className="title">预订方式：</span>{bookingStore.bookingSuccessInfo.bookingType}</li>
						<li><span className="title">预  订 金：</span>{bookingStore.bookingSuccessInfo.bookingAmount}</li>
					
					</ul>
					{false&&<div className="receipt-payment">
						<div className="payment">
							<span>支付方式：</span>
							<ul>
								<li>储蓄卡</li>
								<li>现金</li>
							</ul>
						</div>
						<div className="money">
							<span>金额：</span>
							<ul>
								<li>1680</li>
								<li>1000</li>
							</ul>
						</div>
					</div>}
					<div className="receipt-remarks">
						<div className="remarks">
							<span className="title">备注：</span>
							{bookingStore.bookingSuccessInfo.memo}
						</div>
						<ul className="person">
							<li><span className="title">建单人：</span>{bookingStore.bookingSuccessInfo.customerName}</li>
							<li><span className="title">建单时间：</span>{bookingStore.bookingSuccessInfo.createTime}</li>
						</ul>					
					</div>
					<div className="receipt-address">
						<div><span className="title">餐厅地址：</span>{bookingStore.bookingSuccessInfo.managerPhone}</div>
						<div><span className="title">联系电话：</span>{bookingStore.bookingSuccessInfo.postalAddress}</div>
					</div>
					<div className="receipt-notice">
						注意：您选择了预订点菜，商家保留预订桌台及预订菜品，过期最长保留XX分钟，提前一天及以上取消预订，全额退还支付金额，否则不退！请知悉
					</div>

					<div className="confirm-btn" onClick={this.okClick}>确定</div>
				</div>
			</Modal>
      </div>
	}

}

export default PayReceiptPopup;



